<script>
import PieChart from "../../components/PieChart/index";
import LinesChart from "../../components/LineChart1/index";
import { initData } from "../../api/inidata";

export default {
  name: "index",
  components: {
    PieChart,
    LinesChart,
  },
  data() {
    return {
      loading: true,
      time: "",
      date: "",
      week: "",
      title: "数字白马·基层单元智治中心",
      activeName: "first",
      scoresList: [],
      options: {
        tooltip: {
          trigger: "axis",
        },
        color: ["#c44b7d", "#3fafec"],
        grid: {
          left: "3%",
          right: "4%",
          bottom: "4%",
          containLabel: true,
        },
        toolbox: {},
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: [
            "2021一季度",
            "2021二季度",
            "2021三季度",
            "2021四季度",
            "2022一季度",
            "2022二季度",
          ],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            name: "女",
            type: "line",
            stack: "Total",
            data: [1000, 2000, 3000, 3500, 4100, 4200, 4400, 4500],
          },
        ],
      },
      options1: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        legend: {},
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "value",
          boundaryGap: [0, 0.01],
        },
        yAxis: {
          type: "category",
          data: [
            "2021一季度",
            "2021二季度",
            "2021三季度",
            "2021四季度",
            "2022一季度",
            "2022二季度",
          ],
        },
        series: [
          {
            color: "#02cde6",
            height: "5px",
            name: "",
            type: "bar",
            data: [1000, 2000, 3000, 3500, 4100, 4200, 4400, 4500],
          },
        ],
      },
      options2: {
        radar: {
          // shape: 'circle',
          indicator: [
            { name: "就业率" },
            { name: "待岗率" },
            { name: "未就业率" },
            { name: "就业投诉" },
            { name: "退休率" },
          ],
        },
        series: [
          {
            name: "Budget vs spending",
            type: "radar",
            data: [
              {
                value: [4200, 3000, 20000, 35000, 50000],
              },
            ],
          },
        ],
      },
      data: [],
    };
  },
  mounted() {
    this.cancelLoading();
    setInterval(this.getTime, 1000);
    this.getScoresData();
  },
  methods: {
    getScoresData() {
      initData(
        "http://47.100.166.173:8099/qujiangqu/api/users/statistics/scores/list",
        { num: 10 }
      ).then((res) => {
        console.log(res.data);
        this.scoresList = res.data;
      });
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    cancelLoading() {
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
    getTime() {
      var date = new Date();
      var YY = date.getFullYear();
      var MM =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var DD = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      var hh = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      var mm =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      var ss =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      var myddy = date.getDay(); //获取存储当前日期
      var weekday = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      this.time = hh + ":" + mm + ":" + ss;

      this.date = YY + "年" + MM + "月" + DD + "号";
      this.week = weekday[myddy];
    },
  },
};
</script>

<style lang="scss">
@import "../../assets/style/index.scss";

.header-box {
  width: 100%;
  height: 15vh;
  position: relative;

  .return {
    position: absolute;
    top: 5rem;
    right: 200px;
    width: 100px;
    height: 50px;
    border: 2px solid #999999;
    border-radius: 3px;
    display: flex;

    span {
      display: flex;
      font-size: 20px;
      vertical-align: center;
      align-items: center;
    }
  }

  .time-box {
    width: 15%;
    height: 5vh;
    position: absolute;
    top: 5rem;
    left: 3rem;

    > div {
      display: inline-block;
    }

    .time {
      color: #64e5ce;
      font-size: 28px;
      margin-left: 0.2rem;
    }

    .date {
      color: #daf4ef;
      font-size: 12px;
    }
  }

  .title-box {
    position: absolute;
    top: 2.5rem;
    left: 39%;

    span {
      color: #ffffff;
      font-size: 40px;
      font-weight: bold;
      display: flex;
    }
  }
}

.content-box {
  height: 100%;
  padding: 10px 30px 0 30px;
  position: relative;

  .box-page {
    padding: 0 0 0 20px;
    img {
      padding-top: 25px;
      padding-left: 25px;
    }
  }

  .name1 {
    font-size: 20px;
    color: #18a1df;
    padding: 15px 0 10px 20px;
  }

  .cunjiti {
    width: 420px;
    height: 350px;
    //border: 1px solid #257dff;
    background-color: rgba(16, 27, 47, 0.8);
  }

  .shehuifuwuxin {
    width: 750px;
    height: 150px;
    background-color: rgba(16, 27, 47, 0.8);
  }
  .nongcanping {
    width: 750px;
    height: 570px;
    background-color: rgba(16, 27, 47, 0.8);
    .el-tabs__item {
      color: #ffffff;
    }
  }
}
</style>



