<!--main-->
<template>
  <div>
    <v-chart
      ref="ref_echart1"
      :option="option_column"
      
      style="height: 400px"
    ></v-chart>
  </div>
</template>
<script>
import "echarts/lib/chart/pie";
import "echarts/lib/component/tooltip";
export default {
  props: {
    options: {
      type: Object,
    },
  },
  data() {
    return {
      option_column: {
        tooltip: {},
        series: [
          {
            name: "销量",
            type: "pie",
            color: ["#18A1DF", "#00E676", "#FFEA00", "#FF9100", "#FF3D00"],
            data: [
              {
                value: this.options["人在户在"],
                name: "人在户在",
              },
              {
                value: this.options["人在户不在"],
                name: "人在户不在",
              },
              {
                value: this.options["人不在户在"],
                name: "人不在户在",
              },
              {
                value: this.options["人户都不在"],
                name: "人户都不在",
              },
              {
                value: this.options["租户"],
                name: "租户",
              },
            ],
            label: {
              normal: {
                textStyle: {
                  color: "#fff",
                  fontSize: "20px",
                },
                formatter: "{b}{d}%",
              },
            },
          },
        ],
      },
    };
  },
  mounted() {
    window.addEventListener("resize", () => this.$refs.ref_echart1.resize());
  },
  methods: {},
};
</script>



