<!--main-->
<template>
  <div>
    <v-chart
      ref="ref_echart1"
      :option="option_column"
      :style="{height: getHeight()}"
    ></v-chart>
  </div>
</template>
<script>
import 'echarts/lib/chart/line'
import 'echarts/lib/component/tooltip'

export default {
  props:{
    options:{
      type: Object
    },
    height:{
      type: String
    }
  },
  data() {
    return {
      option_column: {
        tooltip: {
          trigger: 'axis',

        },
        color: ['#c44b7d', '#3fafec'],
        legend: {
          textStyle: {
            color: '#999999',
            fontSize: '20px'
          }

        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '4%',
          containLabel: true
        },
        toolbox: {},
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            type: 'line',
            stack: 'Total',
            data: []
          },
          {
            type: 'line',
            stack: 'Total',
            data: []
          }
        ]
      },
    };
  },
  created() {
    this.option_column=this.options;
  },
  mounted() {
    window.addEventListener("resize", () => this.$refs.ref_echart1.resize());
  },
  methods: {
    getHeight(){
      return this.height
    }
  },
};
</script>



